@charset "UTF-8";

/* 레이아웃 */

body {
  font-size: 12px;
  font-family: dotum, sans-serif;
  color: #666;
  line-height: 1.2em;
}

body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
form,
fieldset,
input,
textarea,
blockquote,
th,
td,
p {
  margin: 0px;
  padding: 0px;
}

fieldset,
img,
abbr,
acronym {
  border: none;
}

address,
caption,
cite,
code,
dfn,
var {
  font-style: normal;
  font-weight: normal;
}

ol,
ul {
  list-style: none;
}

caption {
  /*visibility:hidden;*/
  overflow: hidden;
  width: 0;
  height: 0;
  font-size: 0;
  line-height: 0;
  text-indent: -9999px;
}

legend {
  visibility: hidden;
  position: absolute;
  top: -9999em;
  left: -9999em;
  width: 0;
  height: 0;
  line-height: 0;
  overflow: hidden;
}

a {
  text-decoration: none;
  color: #666;
}

a:hover {
  color: #666;
  text-decoration: none;
}

hr {
  display: none;
}

img {
  vertical-align: top;
}

label,
input,
select {
  vertical-align: middle;
}

select,
input {
  font-size: 12px;
  font-family: dotum, sans-serif;
  color: #666;
}

textarea {
  font-size: 12px;
  font-family: dotum, sans-serif;
  color: #666;
}

select {
  margin: 0;
}

form {
  line-height: 1.2em !important;
}

.list_normal {
  margin: 5px 0;
}

.list_normal li {
  padding: 2px 0 2px 7px;
  background: url("http://image.kyobobook.co.kr/ink/images/common/icon_squ.gif") no-repeat 0 6px;
  font-size: 12px;
  line-height: 15px;
}

.list_normal.border_top {
  border-top: 1px solid #ddd;
  padding-top: 10px;
}

.margin_top5 {
  margin-top: 5px !important;
}

.margin_top20 {
  margin-top: 20px !important;
}

.align_center {
  text-align: center !important;
}

.font_orange {
  color: #ff6600 !important;
}

/* 텍스트 버튼 기본형 */

.btn_small {
  display: inline-block;
  white-space: nowrap;
  vertical-align: baseline;
  *vertical-align: -2px;
  border: 1px solid #d0d0d0;
  padding: 5px 4px 0px;
  height: 13px;
  font-size: 11px !important;
  line-height: 13px;
  font-weight: normal;
  font-family: "dotum", sans-serif;
  color: #666 !important;
  text-decoration: none !important;
  background-color: #fff;
  text-align: center;
  transition: all 0.2s;
}

.btn_small:hover {
  border-color: #a0a0a0;
  text-decoration: none;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.1);
}

.btn_medium {
  display: inline-block;
  white-space: nowrap;
  vertical-align: middle;
  border: 1px solid #aaa;
  padding: 7px 8px 0;
  height: 18px;
  font-size: 12px !important;
  line-height: 15px;
  font-weight: bold;
  font-family: "dotum", sans-serif;
  color: #555 !important;
  text-decoration: none !important;
  background-color: #fff;
  text-align: center;
  transition: all 0.2s;
}

.btn_medium:hover {
  border-color: #777;
  text-decoration: none;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.15);
}

.btn_large {
  display: inline-block;
  white-space: nowrap;
  vertical-align: middle;
  border: 1px solid #aaa;
  padding: 8px 12px 0;
  height: 22px;
  font-size: 14px !important;
  line-height: 18px;
  font-weight: bold;
  font-family: "dotum", sans-serif;
  color: #555 !important;
  text-decoration: none !important;
  background-color: #fff;
  text-align: center;
  transition: all 0.2s;
}

.btn_large:hover {
  border-color: #777;
  text-decoration: none;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
}

.btn_xlarge {
  display: inline-block;
  white-space: nowrap;
  vertical-align: middle;
  border: 1px solid #aaa;
  padding: 16px 25px 0;
  height: 28px;
  font-size: 14px !important;
  line-height: 18px;
  font-weight: bold;
  font-family: "dotum", sans-serif;
  color: #555 !important;
  text-decoration: none !important;
  background-color: #fff;
  text-align: center;
  transition: all 0.2s;
}

.btn_xlarge:hover {
  border-color: #777;
  text-decoration: none;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
}

.btn_xxlarge {
  display: inline-block;
  white-space: nowrap;
  vertical-align: middle;
  border: 1px solid #aaa;
  padding: 19px 25px 0;
  height: 39px;
  font-size: 16px !important;
  line-height: 20px;
  font-weight: bold;
  font-family: "Malgun Gothic", sans-serif;
  color: #555 !important;
  text-decoration: none !important;
  background-color: #fff;
  text-align: center;
  transition: all 0.2s;
}

.btn_xxlarge:hover {
  border-color: #777;
  text-decoration: none;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
}

/* 텍스트 버튼 특수형 */

.btn_small2 {
  display: inline-block;
  vertical-align: middle;
  border: 1px solid #b4b4b4;
  border-radius: 2px;
  padding: 5px 6px 0;
  min-width: 20px;
  height: 13px;
  background-color: #f8f8f8;
  font: 11px/13px dotum, sans-serif;
  color: #626262 !important;
  letter-spacing: -1px;
  text-align: center;
  text-decoration: none;
  background: #f4f4f4 url("http://image.kyobobook.co.kr/ink/images/common/btn_small2.gif") repeat-x;
  box-shadow: inset 1px 0 0 #fff, inset -1px 0 0 #fff;
  transition: all 0.2s;
}

.btn_small2:hover {
  border-color: #999;
  text-decoration: none;
}

.btn_small3 {
  display: inline-block;
  vertical-align: middle;
  border: 1px solid #555;
  border-radius: 2px;
  padding: 4px 6px 0;
  height: 13px;
  background-color: #717171;
  font: 11px/13px dotum, sans-serif !important;
  color: #fff !important;
  text-decoration: none !important;
  text-indent: 0;
}

.btn_small3:hover {
  border-color: #333;
  text-decoration: none !important;
}

.btn_small3.btn_blue {
  border-color: #475178;
}

.btn_small3.btn_blue:hover {
  border-color: #323955;
}

.btn_small3.btn_blue2 {
  border-color: #5c6a9d;
}

.btn_small3.btn_blue2:hover {
  border-color: #404a6e;
}

.btn_small3.btn_red {
  border-color: #c10714;
}

.btn_small3.btn_red:hover {
  border-color: #83252b;
}

.btn_small.btn_contract,
.btn_small2.btn_contract {
  padding: 3px 3px 0;
  height: 11px;
  margin: 1px 0px;
  *margin: 0;
  *vertical-align: baseline;
}

#container .btn_small.btn_contract,
#container .btn_small2.btn_contract {
  height: 11px !important;
}

/* cartlist.css */

input.btn_small,
button.btn_small {
  height: 20px;
  *padding-top: 3px;
  *overflow: visible;
}

input.btn_medium,
button.btn_medium {
  height: 24px;
  padding-top: 5px;
  *padding-top: 3px;
  *overflow: visible;
}

input.btn_large,
button.btn_large {
  height: 32px;
  padding-top: 6px;
  *padding-top: 4px;
  *overflow: visible;
}

input.btn_xlarge,
button.btn_xlarge {
  height: 46px;
  padding-top: 7px;
  *padding-top: 5px;
  *overflow: visible;
}

input.btn_xxlarge,
button.btn_xxlarge {
  height: 58px;
  padding-top: 0;
  *overflow: visible;
}

span.btn_small:hover {
  border-color: #d0d0d0;
  box-shadow: none;
}

