/* FireFox */

@-moz-document url-prefix()  {
  .btn_small {
    padding: 3px 4px 2px;
  }

  .btn_small2 {
    padding: 3px 6px 2px;
  }

  .btn_small3 {
    padding: 2px 6px 2px;
  }

  .btn_small.btn_contract,
  .btn_small2.btn_contract {
    padding: 1px 3px 2px !important;
  }

  /* cartlist.css */

  .btn_medium {
    padding: 6px 8px 1px;
  }

  .btn_large {
    padding: 7px 12px 1px;
  }

  .btn_xlarge {
    padding: 14px 25px 2px;
  }

  .btn_xxlarge {
    padding: 17px 25px 2px;
  }

  .sort_option a {
    padding: 3px 4px 2px;
  }

  input.btn_small,
  button.btn_small {
    padding: 1px 4px 0;
  }
}

