}

.table_cartlist01 th {
  padding: 7px 0 6px;
  color: #1770b5;
  border-right: 1px solid #dadada;
  border-bottom: 1px solid #dadada;
  background: #f9f9f9;
}

.table_cartlist01 td {
  padding: 6px 10px 5px;
  color: #555;
  border-right: 1px solid #dadada;
  border-bottom: 1px solid #dadada;
  text-align: right;
}

.table_cartlist02 {
